<div class="flex flex-col justify-center p-5 w-full leading-loose md:px-56 md:py-3"><p class="text-2xl font-bold text-center m-12">JerryFriends平台隱私權條款&使用者條款</p><p>本同意書說明JerryFriends平台如何處理本表單所蒐集到的個人資料。當您於註冊及登錄勾選「同意」並時，表示您已閱讀、瞭解並同意接受本同意書之所有內容及其後修改變更規定。若您未滿二十歲，應於您的法定代理人閱讀、瞭解並同意本同意書之所有內容及其後修改變更規定後，方得使用本服務，但若您已接受本服務，視為您已取得法定代理人之同意，並遵守以下所有規範。</p><p>基本資料之蒐集、更新及保管</p><p>JerryFriends平台蒐集您的個人資料在中華民國「個人資料保護法」與相關法令之規範下，依據本院【隱私權政策聲明】，蒐集、處理及利用您的個人資料。</p><p>請於申請時提供您本人正確、最新及完整的個人資料。</p><p>JerryFriends平台因執行業務所蒐集您的個人資料包括姓名、電話、電子信箱、地址等。</p><p>若您的個人資料有任何異動，請主動向本院申請更正，使其保持正確、最新及完整。</p><p>若您提供錯誤、不實、過時或不完整或具誤導性的資料，您將損失相關權益。</p><p>您可依中華民國「個人資料保護法」，就您的個人資料行使以下權利：</p><p>請求查詢或閱覽。(2)製給複製本。(3)請求補充或更正。(4)請求停止蒐集、處理及利用。(5)請求刪除。</p><p>但因JerryFriends平台執行職務或業務所必須者，JerryFriends平台得拒絕之。若您欲執行上述權利時，請參考本院【隱私權政策聲明】之個人資料保護聯絡窗口聯絡方式與本院連繫。個人資料保護申訴電子郵件信箱：achieveJerryFriends@gmail.com。但因您行使上述權利，而導致權益受損時，本院將不負相關賠償責任。</p><p>蒐集個人資料之目的</p><p>JerryFriends平台為執行身分瞭解蒐集您的個人資料，也可於設定中自訂外部可見訊息，JerryFriends平台不會利用任何方式將您的個人資料給予其他人，除非經過您的同意。</p><p>當您的個人資料使用方式與當初JerryFriends平台蒐集的目的不同時，我們會在使用前先徵求您的書面同意，您可以拒絕向JerryFriends平台提供個人資料，但您可能因此喪失您的權益。</p><p>JerryFriends平台利用您的個人資料期間若隱私權條款或使用者條款有變更，將會直接更新於網站內部，不再另外進行通知。</p><p>基本資料之保密</p><p>您的個人資料受到JerryFriends平台【隱私權政策聲明】之保護及規範。請閱讀【隱私權政策聲明】以查閱JerryFriends平台完整【隱私權政策聲明】。JerryFriends平台如違反「個人資料保護法」規定或因天災、事變或其他不可抗力所致者，致您的個人資料被竊取、洩漏、竄改、遭其他侵害者，JerryFriends平台將於查明後以電話、信函、電子郵件或網站公告等方法，擇適當方式通知您。</p><p>同意書之效力</p><p>當您勾選「同意」時，即表示您已閱讀、瞭解並同意本同意書之所有內容，您如違反下列條款時，JerryFriends平台得隨時終止對您所提供之所有權益或服務。</p><p>JerryFriends平台保留隨時修改本同意書規範之權利，JerryFriends平台將於修改規範時，JerryFriends平台網頁(站)公告修改之事實，不另作個別通知。如果您不同意修改的內容，請勿繼續接受本服務。否則將視為您已同意並接受本同意書該等增訂或修改內容之拘束。</p><p>您自本同意書取得的任何建議或資訊，無論是書面或口頭形式，除非本同意書條款有明確規定，均不構成本同意條款以外之任何保證。</p><p>準據法</p><p>本同意書之解釋與適用，以及本同意書有關之爭議，均應依照中華民國法律予以處理。</p><p>JerryFriends平台隱私權條款&使用者條款</p><p>資料政策</p><p>本政策說明我們為支援JerryFriends平台及其他JerryFriends平台所提供之功能(包括論壇、聊天室、訊息等)所處理的資料。您可以在JerryFriends平台設定找到其他工具和資訊。</p><p>我們打造的眾多服務協助數百人以嶄新方式與他人互動。服務內容主要包含：論壇、聊天室、好友、簡介、行事曆、留言、搜尋功能  </p><p>如果您對本《隱私權政策》有任何疑問，請與我們聯絡。</p><p>JerryFriends平台收集的資訊</p><p>我們希望您瞭解JerryFriends平台當您使用JerryFriends平台服務時收集的資訊類型</p><p>我們收集資訊是為了向所有使用者提供更優質的服務；這些資訊可協助我們判斷基本設定 (例如您使用的語言)，或是在您允許的前提下，展現出您的基本資訊，如：個人資料展現。JerryFriends平台收集的資訊類型及這些資訊的用途取決於您使用JerryFriends平台功能的方式，以及您的隱私權設定。</p><p>當您建立JerryFriends平台帳戶時，系統會要求您提供個人資訊，包括您的姓名、電子郵件地址、密碼。使我們可以向您提供某些資訊，例如要用來接收訂閱報的電子郵件地址，或是後續變更密碼，可用於與您聯繫。</p><p>我們也會搜集您在使用JerryFriends平台時建立、上傳，或從其他帳戶位置收到的內容，包括您的撰寫、收藏、儲存的相片和影片、建立的文件和試算表、添加的好友、創立及加入的聊天室。</p><p>如果您使用JerryFriends平台功能進行通話及訊息時，我們可以收集通話和訊息紀錄資料，例如您的電話號碼、來電號碼、寄件者與收件者電子郵件地址、通話與傳訊日期和時間、通話持續時間、傳送資訊，以及通話和訊息的類型與數量和內容。</p><p>我們運用收集到的資料改善服務</p><p>我們從各項JerryFriends平台收集到的資訊會用於下列用途：</p><p>提供嶄新JerryFriends平台服務</p><p>我們會使用您的資訊來提供JerryFriends平台服務及功能，例如處理您的搜尋字詞來區分類別進行推薦，或者根據您的公開資料建議內容分享對象。</p><p>維護及改善JerryFriends平台服務以及服務</p><p>我們也會使用您的資訊來確保JerryFriends平台服務正常運作，例如追蹤服務中斷事件或解決您回報</p><p>我們不會與廣告客戶分享您的個人任何識別資訊 (例如您的姓名或電子郵件)，但您主動要求分享時除外。舉例來說，如果您看到附近餐廳的廣告並選取 [輕觸通話] 按鈕，我們會為您接通電話，也會將您的電話號碼提供給餐廳。</p><p>我們會對資料進行分析和評估，以瞭解JerryFriends平台服務的使用情形。比方說，我們會分析您在 JerryFriends平台網站的造訪資料，分析結果可用於改善產品設計等用途。此外，我們也會使用您進行互動的廣告相關資料，協助廣告客戶掌握廣告活動成效。</p><p>與您交流互動</p><p>我們會使用收集到的資訊 (例如您的電子郵件地址) 直接與您互動。例如，如果我們偵測到可疑活動，我們會寄送一封電子郵件通知您。此外，我們也可能會針對JerryFriends平台服務的近期異動或改善寄送通知給您。如果您與JerryFriends平台聯絡，我們會記錄您的要求，以助您解決任何可能遇到的問題。</p><p>保護JerryFriends平台使用者</p><p>我們會運用相關資訊來改善JerryFriends平台服務的安全性和穩定性。這包括偵測及預防任何可能會對JerryFriends平台使用者或一般大眾造成傷害的詐欺事件、濫用行為、安全性風險和技術問題，並且做出應變。</p><p>如果其他使用者已經握有您的電子郵件地址或其他個人識別資訊，我們會向他們顯示您的公開JerryFriends平台帳戶資訊 (例如您的姓名和相片)。這有多種用途，如有助於其他使用者辨識您寄出的電子郵件。</p><p>如果我們需要將您的資訊用於本《隱私權政策》未載明的其他目標，將事先徵求您的同意。</p><p>您的隱私權控制項</p><p>您可以管理我們收集的資訊項目和資訊的用途</p><p>本節介紹各個用於管理JerryFriends平台服務隱私權設定的主要控制項。您也可以前往設定，查看及調整重要的隱私權設定。除了這些工具之外，JerryFriends平台產品本身還提供特定的隱私權設定，詳情請參閱 JerryFriends平台使用者條款。</p><p>前往隱私權設定檢查</p><p>管理、查看及更新您的資訊</p><p>登入帳戶後，您隨時可以前往所用服務查看及更新資訊。舉例來說，地址、電話、生日等。都能協助您管理您在JerryFriends平台服務中儲存的特定類型內容。</p><p>您也可以查看和控管JerryFriends平台帳戶中儲存的資訊。您的JerryFriends平台帳戶提供下列設定：</p><p>隱私權設定</p><p>登入帳戶後，您隨時可以前往所用服務查看及更新資訊。舉例來說，地址、電話、生日等。都能協助您管理您在JerryFriends平台服務中儲存的特定類型內容。</p><p>活動設定</p><p>決定要儲存在帳戶中的活動類型。舉例來說，您可以選擇私密訊息、群組或是公開方式。</p><p>廣告設定</p><p>管理在JerryFriends平台上向您顯示的廣告相關偏好設定。您可以修改您的興趣、設定是否要讓系統使用您的個人資訊放送更相關的廣告，以及開啟/關閉特定廣告服務。</p><p>前往廣告設定</p><p>我的活動</p><p>「活動」可讓您查看及控管您使用JerryFriends平台服務時所產生的資料，例如您留言或參與群組的紀錄。您可以依日期和主題瀏覽活動，也可以刪除部分或所有活動紀錄。</p><p>前往「活動」</p><p>帳戶主頁->下方最左邊「活動」按紐</p><p>JerryFriends平台資訊主頁可讓您管理特定產品的相關資訊。</p><p>前往「個人帳戶」</p><p>管理您的聯絡資訊，例如姓名、電子郵件和電話號碼。</p><p>前往「討論群」</p><p>在未登入帳戶的狀態下，您可以管理瀏覽器或裝置的相關資訊，包括：</p><p>查看自薦討論群、提案討論群、文章討論群、經驗分享區內容。</p><p>如要刪除您的資訊，您可以：</p><p>將您的內容從JerryFriends平台功能中刪除</p><p>刪除您的資訊</p><p>最後一項工具刪除帳戶，可讓您在無法繼續使用JerryFriends平台帳戶時，在設定中刪除帳戶。</p><p>事先徵得您的同意</p><p>我們在事先徵得您同意的情況下，可與JerryFriends平台以外的對象分享您的個人資訊。舉例來說，當您於聊天室或個人資料時，我們會先徵得您的同意，再將您的姓名或電話號碼提供給餐廳。我們在分享任何敏感個人資訊前，一律會請您提供明確許可。</p><p>與網域管理員分享</p><p>如果您是學生，或是任職的機構採用JerryFriends平台服務，那麼負責管理帳戶的網域管理員和經銷商將能存取您的JerryFriends平台帳戶。他們可執行的操作可能包括：</p><p>存取及保留您帳戶中儲存的資訊，例如您的電子郵件</p><p>查看您帳戶的相關統計資料，例如您安裝的應用程式數量</p><p>變更您的帳戶密碼</p><p>將您的帳戶暫時或永久停權</p><p>配合適用法律、法規、法律令狀或合法的政府請求接收您的帳戶資訊</p><p>限制您使用刪除/修改個人資訊或隱私權設定的功能</p><p>供外部處理</p><p>我們會將個人資訊提供給JerryFriends平台的關聯企業和JerryFriends平台信任的其他企業或人員進行處理，處理過程必須遵從我們的指示，也必須遵守本《隱私權政策》和任何其他適當的保密和安全措施。舉例來說，我們的客戶服務就有相關服務供應商提供一臂之力。</p><p>基於法律原因</p><p>如果我們確信，基於下列目的存取、使用、保留或揭露個人資訊屬合理必要的行為，我們會將個人資訊提供給JerryFriends平台以外的第三方：</p><p>配合任何適用法律、法規、法律令狀或合法的政府請求。我們會在資訊公開報告中提供各國政府向JerryFriends平台提出的要求數量和類型。</p><p>執行適用的《服務條款》，包括調查可能的違規情事。</p><p>偵測、預防或是處理詐欺、安全性或技術問題。</p><p>依法律規定或在合法範圍內，為JerryFriends平台使用者或一般大眾提供保護，使其權利、財產或安全不致遭受損害。</p><p>我們可公開不含身分識別內容的資訊，或是將這類資訊提供給合作夥伴 (例如發佈商、廣告客戶、開發人員或權利所有人)。舉例來說，我們會藉由公開資訊，讓外界瞭解JerryFriends平台服務的一般使用趨勢。我們也允許特定合作夥伴使用自己的 Cookie 或類似技術，從您的瀏覽器或裝置收集資訊，以便放送廣告及評估各項數據。</p><p>如果JerryFriends平台涉及合併、收購事宜或出售資產，我們會繼續維護您個人資訊的機密性，並於個人資訊移轉或成為其他隱私權政策的涵蓋目標前，先行通知受影響的使用者。</p><p>捍衛資安</p><p>我們的服務中設有安全性措施來保護您的資訊</p><p>所有JerryFriends平台功能皆內建強大的安全性功能，可時時保護您的資訊。我們維護服務時獲得的深入分析資料，有助於我們預先偵測及自動封鎖安全性威脅，站在前線幫您好好把關。如果我們偵測到安全性風險並認為需要通知您，我們會發出通知，同時一步步引導您加強安全性防護。</p><p>我們致力為您和JerryFriends平台提供妥善保護，防止他人未經授權存取、竄改、揭露或毀損我們持有的個人資訊和JerryFriends平台相關資訊。我們採取的措施包括：</p><p>傳輸您的資料時採用加密技術，保障資料的私密性</p><p>提供安全瀏覽、安全設定檢查和兩步驟驗證等多種安全性功能，協助您保護自己的帳戶</p><p>檢查我們的資訊收集、儲存及處理做法 (包括實體安全性措施)，防止他人未經授權存取我們的系統</p><p>我們僅允許 JerryFriends平台 員工、承包商和代理商基於處理資訊之需要存取個人資訊，且相關人員均須遵守嚴格的契約保密義務，一旦未遵守義務便將受到懲戒或解約處分。</p><p>匯出資料</p><p>如要刪除您的資訊，您可以：</p><p>搜尋您帳戶中的特定項目，然後予以刪除</p><p>刪除整個 JerryFriends平台 帳戶</p><p>您可隨時刪除特定資料，例如由您建立或上傳的內容。您也可以刪除帳戶中儲存的活動資訊，或是設定在特定時間後自動刪除該資訊。</p><p>其他資料 (例如伺服器記錄中的廣告資料) 在經過特定時間後會遭到系統自動刪除或匿名處理。</p><p>有些資料會保留到您刪除自己的JerryFriends平台帳戶為止，例如您使用JerryFriends平台服務的頻率資訊。</p><p>有些資料則會依正當業務需求或法律需求 (例如加強安全性、防範詐騙和濫用行為或保存財務記錄) 延長保留期限。</p><p>當您刪除資料時，我們會遵循刪除程序，確實將您的資料從JerryFriends平台伺服器中安全且完整地移除，或是僅保留匿名處理後的資料。我們致力確保JerryFriends平台服務提供妥善保護，防止您的資訊遭到誤刪或惡意刪除。因此，當您刪除某些資訊時，我們的現行和備份系統可能仍會將該資訊的副本保留一段時間才刪除。</p><p>您可以閱讀這篇文章，進一步瞭解JerryFriends平台的資料保留期限規定，包括刪除資訊所需的時間。</p><p>遵循法律並與主管機關合作</p><p>我們會定期審查本《隱私權政策》，並確保根據當中規定的方式處理您的資訊。</p><p>資料移轉</p><p>我們維護的伺服器遍布全球各地，因此您的資訊可能會由您所在國家/地區外的伺服器處理。每個國家/地區各有不同的資料保護法，保護的完善與嚴密程度也各不相同。無論我們是在哪裡處理您的資訊，一律會採用本政策中載明的保護措施。我們也會遵守與資料移轉相關的特定法律架構。</p><p>我們收到正式的書面申訴後，將與申訴人聯絡。我們會與適當的主管機關 (包括當地的資料保護主管機關) 合作，解決任何與您的資料移轉有關且無法由我們直接與您解決的申訴。</p><p>關於本政策</p><p>本政策的適用範圍</p><p>本《隱私權政策》適用於所有由JerryFriends平台及其關係企業 提供的服務，以及在第三方網站提供的服務 (例如廣告服務)。然而，本《隱私權政策》不適用於另行訂定隱私權政策且未納入本《隱私權政策》的服務。</p><p>本《隱私權政策》不適用於下列項目：</p><p>為JerryFriends平台功能製作廣告的公司和機構組織所採用的資訊處理機制</p><p>本政策的變更</p><p>我們會不時修訂本《隱私權政策》。我們不會在未取得您明確同意的情況下，逕自縮減本《隱私權政策》賦予您的權利。我們一定會標明最後修訂日期，並保存政策的封存版本供您查閱。如果本《隱私權政策》有重大異動，我們會提供更明顯的通知 (例如某些服務會以電子郵件通知政策異動)。</p></div>