<div class="p-5 w-full leading-loose md:px-56 md:py-3"><div class="mb-10 mt-5"><p class="text-2xl font-medium mb-5">​關於JF<br>一個新創團隊夥伴牽線平台</p><p class="text-end .text-sm md:text-lg">JF開發團隊 由一群熱愛跨領域的學生組成。我們都有著一致的共識，<br>希望可以讓斜槓青年們有一個加速發展的機會。</p></div><div class="mb-10 flex md:flex-row flex-col"><div class="w-fill md:w-2/3"><p class="text-xl font-medium mb-5">​團隊核心成員<div class="mb-5"><p class="text-lg mb-3 text-gray-900">Jerry | 企劃兼創始人<br></p><p class="text-gray-600 text-sm font-light">​就讀NTUST，發起並參與過多個設計項目。</p><p class="text-gray-600 text-sm font-light mt-3">特殊表現 :</p><p class="text-gray-600 text-sm font-light">2020 IF 設計新秀獎、2021 紅點概念最佳設計獎、GE卓越創新銅獎、2021 工研院創意狂想金獎、2021 美國建築大師最佳設計獎、2021 中華設計銀獎、2022 綠色產品年度最佳獎、​2022 台灣仿生設計永續城市獎、2022 IF 社會影響力獎-入圍</p></div><div class="mb-5"><p class="text-lg mb-3 text-gray-900">Renze | 技術兼共同創始人​</p><p class="text-gray-600 text-sm font-light">就讀NYCU，橘越設計創辦人，專業為產品設計領域。</p><p class="text-gray-600 text-sm font-light mt-3">特殊表現 :</p><p class="text-gray-600 text-sm font-light">2016 無人機創新自造松第一名、2016 通訊大賽-聯發科穿戴裝置開發第一名、2017 (IEYI)世界青少年發明競賽 第一名、2018 中美青年創客大賽 第一名、2018 台中創新設計競賽 第一名、2018 印度(IEYI)世界青少年創客發明競賽 第一名</p></div><div class="mb-5"><p class="text-lg mb-3 text-gray-900">Agbld | 技術兼顧問</p><p class="text-gray-600 text-sm font-light">​就讀NTUST，完成過多個大型跨領域專案。</p><p class="text-gray-600 text-sm font-light mt-3">特殊表現 :</p><p class="text-gray-600 text-sm font-light">2017 國際科學展覽會(TISF)工程科一等獎、2017 英特爾國際科學與工程展覽會(ISEF)嵌入式系統科三等獎、2017 紐倫堡國際發明展(iENA)金牌、2017 教育部技職之光-成就卓越獎、2019 台科X大阪工大 專案開發夏令營首獎</p></div></p></div><div class="flex m-5 flex-col w-fill md:w-1/3"><img class="m-2" src="../assets/about_page/a.png"><img class="m-2" src="../assets/about_page/b.png"></div></div><div class="mb-10"><p class="text-xl font-medium mb-5">夥伴招募</p><p>學校教的沒地方實踐嗎？</p><p>大一有許多閒暇時間想精進網頁？</p><p>如果你已經具備了網頁開發的基礎，</p><p>且有相當的熱情與動力想要在踏入學習系統框架，</p><p>不分年齡、不分科系、不分學校，</p><p class="mb-5">我們歡迎你一同加入JerryFriends開發團隊！</p><router-link class="btn btn-yello h-fit w-fit" to="/joinus">加入JerryFriends開發團隊！<